import React from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, WindowComponent } from 'summer';
import { CLASS_NAME, NaturezaCredito } from '../../../../models/dtos/ger/naturezaCredito';

interface NaturezaCreditoEditViewProps extends EditViewProps<NaturezaCredito> {}

const NaturezaCreditoEditView: React.FC<NaturezaCreditoEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<NaturezaCredito>({
    defaultValues: selectedItem,
    mode: 'onBlur',
  });

  return (
    <WindowComponent title="Manutenção de Natureza de Crédito">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline
              id="codigo"
              label="Código"
              formProps={{ ...formProps, validation: { required: true, maxLength: 2 } }}
              isPrependLabel={true}
              placeholder="Informe o código"
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="descricao"
              label="Descrição"
              formProps={{ ...formProps, validation: { required: true, maxLength: 150 } }}
              isPrependLabel={true}
              placeholder="Informe a descriçâo"
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default NaturezaCreditoEditView;
